.item {
  display: flex;
  align-items: center;
  box-shadow: 0px 2px 1px 0px rgba(46, 47, 66, 0.08),
    0px 1px 1px 0px rgba(46, 47, 66, 0.16),
    0px 1px 6px 0px rgba(46, 47, 66, 0.08);
  padding: 10px 10px;
  background-color: #fff;
  gap: 10px;
}

.status {
  background-color: red;
  width: 18px;
  height: 18px;
  border-radius: 50%;
}

.status.isOnline {
  background-color: green;
}

.avatar {
  height: 70px;
  width: 70px;
  border-radius: 2%;
}

.name {
  font-weight: 600;
  font-size: 18px;
}
